.Settings {
  background-color: #cccccc;
  vertical-align: top;
  display: inline-block;
  padding: 10px;
}

.SettingsGroup {
  margin-top: 10px;
  margin-bottom: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  max-width: 400px;
}

.PlayersSettings {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: left;
      justify-content: left;
  width: 400px;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.playerSettings {
  display: block;
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 10px;
  border: 1px solid black;
  display: inline-block;
  -ms-flex-pack: left;
      justify-content: left;
  width: 200px;
}




